<template>
    <div class="search-model" v-title data-title="搜索车型">

        <!-- search -->
        <div class="clearfix search-box">
            <div class="col-xs-12 m-t-25">
                <div class="input-group input-group-lg">
                    <input type="text" class="form-control input-text" maxlength="20" placeholder="输入车型查询配件，如CRV、杰德" v-model="modelSearch" v-on:input="vinInputFn()">
                    <span class="input-group-btn">
                        <span class="iconfont icon-dela" @click="clearInputFn" v-if="modelSearch"></span>
                        <button class="btn btn-blue input-text" type="button" v-on:click="searchFn()">查询</button>
                    </span>
                </div>
                <div class="rel m-t-20">
                    <ul class="search-list" v-if="modelSearch && modelSearchList.length > 0">
                        <li class="list cur ell" v-for="item in modelSearchList" :key="item.cmBrand" @click="routeToChooseCondition(item)">
                            <span v-text="item.cm_brand + ' ' + item.cm_factory + ' ' + item.cm_model"></span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <!-- <div class="m-t-10 text-center c6" v-if="searchState || (modelSearch && modelSearchList.length == 0)">
            <span class="iconfont icon-point f60 vm"></span> <span class="vm f36">暂无该车型</span>
        </div> -->

        <!-- 没有匹配数据 -->
        <no-data v-if="searchState || (modelSearch && modelSearchList.length == 0)"></no-data>

    </div>
</template>

<script>
import pageSet from '../js/searchModel';

export default {
    name: 'searchModel',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../../assets/css/config.scss";
.input-text.form-control,
.input-group-btn .input-text.btn {
    font-size: $s28;
}

.icon-dela{
    position: absolute;
    z-index: 10;
    left: -$s40;
    top: $s35;
    font-size: $s24;
    color: $color-C;
}

.search-list{
    border: solid 1px $color-C;
    background-color: $color-F;
}
.search-list .list {
    font-size: $s30;
    padding: $s15 $s15;
    border-bottom: solid 1px $color-C;
    color: $color-6;
}
</style>



