<template>
    <div v-title data-title="选择车型">

        <div class="p-15" v-if="!noDataState">
            <ul class="list-group"
            v-for="factory in carModelList"
            v-bind:key="factory">
                <li class="list-group-item-heading list-group-item" v-text="factory.cm_factory"></li>
                <li class="list-group-item cur"
                v-for="model in factory.list"
                v-bind:key="model"
                v-on:click="routeTo('chooseCondition', factory, model)">
                    <span v-text="model.cm_model"></span>
                </li>
            </ul>
        </div>

        <!-- 没有匹配数据 -->
        <no-data v-else></no-data>

    </div>
</template>

<script>
import pageSet from '../js/chooseModel';

export default {
    name: 'chooseModel',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../../assets/css/config.scss";
.list-group-item-heading {
    margin-bottom: 0;
    background-color: $color-blue;
    color: $color-F;
}
</style>



