import { Toast } from 'mint-ui';
import { mapGetters } from 'vuex';
import * as types from '@/vuex/modules/soupei-types.js';
import router from '@/router';
import errCode from '@/assets/js/errCode';
import ENV from '@/assets/js/env';
import Util from '@/assets/js/utility';

export default {
    name: 'query',
    data() {
        return {
            categoryList: null,
            pageState: null,
            noDataState: false
        };
    },
    mounted() {
        this.$nextTick(() => {
            // 加载品类数据
            this.getCategoryFn();
        });
    },
    computed: {
        ...mapGetters([types.DONE_CATEGORY, types.DONE_CURRENT_TENANT])
    },
    methods: {
        // 加载品类数据
        getCategoryFn() {
            this.$http.get(ENV.queryApi + 'category/' + this.$route.query.tenantId + '/' + this.$route.query.brandId)
                .then((res) => {
                    if (res.data.code === 0) {

                        if(res.data.data.length == 0){
                            this.noDataState = true;
                        }
                        if (res.data.data.length == 1) {

                            // 只有单品类
                            res.data.data[0].toggleHide = true;
                            this.routeToReplace('queryCategory', res.data.data[0]);

                        } else if (res.data.data.length > 1) {

                            // 获取上次该品类页面访问的本地存储对象ID
                            let localCategory = Util.getLocalCategory(res.data.data, this.$route.query.tenantId, this.$route.query.brandId);
                            if (localCategory) {
                                // 有存储过的访问记录
                                let localData = JSON.parse(localStorage.getItem(localCategory));
                                // 如果以前是单品类，现在为多品类则不需要跳转,，让用户选择一次
                                if (!localData.toggleHide) {
                                    this.routeToReplace('queryCategory', localData);
                                    return;
                                }
                            }

                            // 不跳转的多品类显示逻辑
                            if (res.data.data.length <= 4) {
                                // 品类数量 <= 4
                                this.categoryList = res.data.data;
                                // 配置图标
                                this.iconConfig(this.categoryList);
                                // 页面状态
                                this.pageState = 0;
                            } else {
                                // 品类数量 > 4
                                let initData = res.data.data;
                                this.categoryList = [];
                                // 配置图标
                                this.iconConfig(initData);
                                // 重新构造数据
                                this.splicListFn(initData);
                                // 页面状态
                                this.pageState = 1;
                            }

                        }

                    } else {
                        Toast({
                            message: errCode(res.data.code),
                            iconClass: 'iconfont icon-point f80'
                        });
                    }
                })
                .catch(() => {
                    Toast({
                        message: errCode(),
                        iconClass: 'iconfont icon-point f80'
                    });
                });
        },

        // 根据品类json文件，配置图标
        iconConfig(categoryList) {
            for (let i = 0; i < categoryList.length; i++) {
                for (let j = 0; j < this.DONE_CATEGORY.length; j++) {
                    if (categoryList[i].category_id == this.DONE_CATEGORY[j].category_id) {
                        categoryList[i].category_icon = this.DONE_CATEGORY[j].category_icon;
                    }
                }
            }
        },

        // 品类数量 > 4 重新构造数据
        splicListFn(initData) {
            let len = 5;
            let n = 0;
            for (let i = 0; i < initData.length; i++) {
                if (i <= len) {
                    if (!this.categoryList[n]) {
                        this.categoryList[n] = [];
                    }
                    this.categoryList[n].push(initData[i]);
                    if (i % len == 0 && i > 0) {
                        n++;
                        len += 6;
                    }
                }
            }
        },

        // 本地存储选择的品类
        setLocalCategory(item) {
            localStorage.setItem('set|' + this.$route.query.tenantId + '|' + this.$route.query.brandId + '|' + item.category_id, JSON.stringify(item));
        },

        // 跳转页面
        routeTo(url, item) {
            // 本地存储选择的品类
            this.setLocalCategory(item);
            // 跳转品类查询页面，保留历史路径
            router.push({
                name: url,
                query: {
                    tenantId: this.$route.query.tenantId,
                    brandId: this.$route.query.brandId,
                    categoryId: item.category_id,
                    categoryCode: item.category_code
                }
            });
        },

        // 跳转页面(replace方式)
        routeToReplace(url, item) {
            // 本地存储选择的品类
            this.setLocalCategory(item);
            // 跳转品类查询页面，替换历史路径
            router.replace({
                path: 'queryCategory',
                query: {
                    tenantId: this.$route.query.tenantId,
                    brandId: this.$route.query.brandId,
                    categoryId: item.category_id,
                    categoryCode: item.category_code
                }
            });
        }

    }
};



