<template>
    <div class="p-15" v-title :data-title="$route.name == 'searchVin' ? '按VIN查询' : '按编码查询'">

        <div class="hella-bg trans"></div>

        <!--logo-->
        <h1 class="toTopRight-deg logo-box text-center">
            <img class="logo" v-bind:src="DONE_CURRENT_TENANT.brand_logo">
        </h1>

        <!--title-->
        <div class="bg-F1-15"></div>
        <div class="title-badge"><span>{{$route.name == 'searchVin' ? '按VIN查询' : '按编码查询'}}</span></div>

        <!--搜索图标-->
        <div class="text-center code-search-top">
            <span class="iconfont"
            v-bind:class="{'icon-VINchaxun hella-blue': $route.name == 'searchVin', 'icon-bianmachaxun hella-blue': this.$route.name == 'searchCode'}"></span>
        </div>

        <!--VIN查询-->
        <div class="col-xs-12 clearfix">
            <div class="clearfix search-bg m-t-35" v-if="$route.name == 'searchVin'">
                <div class="col-xs-9">
                    <input type="text" class="form-control search-input" placeholder="输入车架号查询配件用品信息"
                        maxlength="20"
                        v-model="vinCode"
                        v-on:input="vinInputFn()"
                    />
                </div>
                <div class="col-xs-3">
                    <button class="btn btn-hella-blue search-btn" type="button"
                    v-on:click="searchFn($route.name)"
                    v-bind:disabled="vinCode.length < 20">
                        <span class="iconfont icon-chaxunzongshu f28 m-r-5"></span>查询
                    </button>
                </div>
            </div>
        </div>

        <!--编码查询-->
        <div class="col-xs-12 clearfix">
            <div class="clearfix search-bg m-t-35" v-if="$route.name == 'searchCode'">
                <div class="col-xs-9">
                    <input type="text" class="form-control search-input" placeholder="输入编码查询配件用品信息"
                         maxlength="50"
                        v-model="partskuCode"
                        v-on:input="partskuCodeInputFn()"
                    />
                </div>
                <div class="col-xs-3">
                    <button class="btn btn-hella-blue search-btn" type="button"
                    v-on:click="searchFn($route.name)">
                        <span class="iconfont icon-chaxunzongshu f28 m-r-5"></span>查询
                    </button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import pageSet from '../js/search';

export default {
    name: 'search',
    data: pageSet.data,
    watch: pageSet.watch,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../../assets/css/config.scss";

.hella-bg{top:1.7rem;}

.logo-box {
    padding-top: $s20;
    padding-bottom: $s20;
    margin: 0;
    border-radius: $s10;
}

.logo {
    height: $s100;
}

.toTopRight-deg{
  background-image:-webkit-linear-gradient(90deg, #fdd000, #ffe090);
  background-image:linear-gradient(90deg, #fdd000, #ffe090);
}

.title-badge{
    width: $s240;
    margin: 0 auto;
    border-top: $s60 solid $color-hella-blue;
    border-right: $s20 solid transparent;
    border-left: $s20 solid transparent;
    position: relative;
    padding-bottom: $s20;
    span {
        position: absolute;
        color: $color-F;
        top: -$s50;
        display: block;
        width: 100%;
        text-align: center;
    }
}


.search-bg {
    background-color: $color-hella-blue;
    padding-top: $s10;
    padding-bottom: $s10;
    border-radius: $s8;
    .col-xs-9{
        padding-left: $s15;
        padding-right: $s15;
    }
    .col-xs-3{
        padding-left: 0;
        padding-right: $s15;
    }
}

.search-input {
    border-radius: 0;
    border: none;
    height: $s60;
}

.search-btn {
    height: $s60;
}

.code-search-top {
    padding-top: $s65;
    padding-bottom: $s35;
    .iconfont {
        font-size: $s300;
    }
}

</style>



