// 设定title
function returnTitle(title, tenant) {
    let obj = {
        '121265185512782850': '海拉ATF查询系统'
    };
    return tenant && obj[tenant.tenant_id] ? obj[tenant.tenant_id] : title;
}

// 验证空对象{}
function isEmptyObject(o) {
    var t;
    for (t in o)
        return !1;
    return !0;
}

// 过滤变速器档位数显示
function filterCarmodelVal(str) {
    if (isNaN(str - 0)) {
        // 转字符串为数字
        return str;
    } else {
        // 拼接中文字符
        return str + '档';
    }
}

// 监听页面滚动设置hellaBg
function hellaBgScroll() {
    let timeout;
    window.onscroll = () => {
        clearTimeout(timeout);
        timeout = setTimeout(() => {
            scrollHandle();
        }, 10);
    };

    function scrollHandle() {
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let hellaBg = document.querySelector('.hella-bg');
        if (scrollTop <= 20) {
            hellaBg.style.top = 1.7 + 'rem';
            hellaBg.style.borderRadius = 0.1 + 'rem';
        }

        if (scrollTop > 20) {
            hellaBg.style.top = 0;
            hellaBg.style.borderRadius = 0 + 'px';
        }
    }
}

// 按数字从小到大排序value(更多筛选条件页面)
function sortByValue(a, b) {
    let result = parseFloat(a.cm_model_value) - parseFloat(b.cm_model_value);
    return result;
}

// 按车系排序(选择品牌页面)
function sortBySerial(a, b) {
    var sorter = {
        '日韩车系': 0,
        '欧美车系': 1,
        '国产车系': 2,
    };
    return sorter[a.cm_brand_country] - sorter[b.cm_brand_country];
}

// talkingdata数据上报
function onEvent(keyword, Label) {
    try {
        if (Label) {
            TDAPP.onEvent(keyword, Label);
        } else {
            TDAPP.onEvent(keyword);
        }
    } catch (error) {
        console.log(error);
    }
}

// 获取上次该品类页面访问的本地存储对象ID
function getLocalCategory(data, tenantId, brandId) {
    for (let key in localStorage) {
        let k = key.split('|');
        if (tenantId == k[1] && brandId == k[2]) {
            for (let i = 0; i < data.length; i++) {
                if (data[i].category_id == k[3]) {
                    return key;
                }
            }
        }
    }
}

// 去左右空格
function trim(s){
    return s.replace(/(^\s*)|(\s*$)/g, '');
}


/**
 * 原生js addClass removeClass hasClass
 */
function hasClass(elem, cls) {
    cls = cls || '';
    if (cls.replace(/\s/g, '').length == 0) return false; //当cls没有参数时，返回false
    return new RegExp(' ' + cls + ' ').test(' ' + elem.className + ' ');
}

function addClass(ele, cls) {
    if (!hasClass(ele, cls)) {
        ele.className = ele.className == '' ? cls : ele.className + ' ' + cls;
    }
}

function removeClass(ele, cls) {
    if (hasClass(ele, cls)) {
        var newClass = ' ' + ele.className.replace(/[\t\r\n]/g, '') + ' ';
        while (newClass.indexOf(' ' + cls + ' ') >= 0) {
            newClass = newClass.replace(' ' + cls + ' ', ' ');
        }
        ele.className = newClass.replace(/^\s+|\s+$/g, '');
    }
}

export default {
    returnTitle: returnTitle,
    isEmptyObject: isEmptyObject,
    filterCarmodelVal: filterCarmodelVal,
    hellaBgScroll: hellaBgScroll,
    sortByValue: sortByValue,
    sortBySerial: sortBySerial,
    getLocalCategory: getLocalCategory,
    trim: trim,
    addClass: addClass,
    removeClass: removeClass,
    onEvent: onEvent
};




