<template>
    <div class="account-tip">
        <div class="account-tip-box">
            <span class="iconfont icon-dela" @click="closeFn"></span>
            <span class="iconfont icon-weifabushangpin f80 vm blue"></span>
            <span class="f30 vm">该商户账号仍在审核或已失效</span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AccountTip',
    data() {
        return {

        };
    },
    mounted: function () {
        this.$nextTick(() => {

        });
    },
    methods: {
        closeFn() {
            this.$emit('message');
        }
    }

};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.account-tip {
    background: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 10;
    left: 0;
    top: 0;
    max-width: 7.2rem;
    margin: 0 auto;
}

.account-tip-box {
    text-align: center;
    background-color: $color-F;
    padding: 25px;
    width: 80%;
    margin: -$s100 auto 0 auto;
    position: relative;
    top: 50%;
}

.icon-dela {
    position: absolute;
    right: $s12;
    top: $s12;
    font-size: $s20;
    color: $color-C;
}
</style>



