import { Toast } from 'mint-ui';
import router from '@/router';
import ENV from '@/assets/js/env';
import errCode from '@/assets/js/errCode';
import Util from '@/assets/js/utility';
let timer;

export default {
    name: 'searchModel',
    data() {
        return {
            modelSearch: '',
            modelSearchList: [],
            searchState: true
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        // 确定搜索
        searchFn() {
            if(this.modelSearchList.length == 1 ){
                this.routeToChooseCondition(this.modelSearchList[0]);
                // 数据上报
                Util.onEvent('通过搜索车型');
            }
        },

        // 搜索输入匹配
        vinInputFn() {
            this.isSearchState();
            this.handleSearch();
        },

        // 处理延时请求搜索接口
        handleSearch() {
            clearTimeout(timer);
            timer = setTimeout(() => {
                this.getSearchFn();
            }, this.delay);
        },

        // 获取搜索结果/query/model/search?keywords
        getSearchFn() {
            let modelSearch = Util.trim(this.modelSearch);
            if(!modelSearch) return;

            this.$http.get(ENV.queryApi + 'query/model/search/' + this.$route.params.tenantId + '/' + this.$route.params.brandId + '?keywords=' + modelSearch)
                .then((res) => {
                    if (res.data.code === 0) {
                        this.modelSearchList = res.data.data;
                    } else {
                        Toast({
                            message: errCode(res.data.code),
                            iconClass: 'iconfont icon-point f80'
                        });
                    }
                })
                .catch(() => {
                    Toast({
                        message: errCode(),
                        iconClass: 'iconfont icon-point f80'
                    });
                });
        },

        // 清除输入框
        clearInputFn(){
            this.isSearchState();
            this.modelSearch = '';
        },

        // 改变初始化无搜索结果状态
        isSearchState() {
            if(this.searchState){
                this.searchState = false;
            }
        },
        // 搜索结果跳转
        routeToChooseCondition(item){
            // 数据上报
            Util.onEvent('通过搜索车型');
            router.push({
                name: 'chooseCondition',
                params: {
                    tenantId: this.$route.params.tenantId,
                    brandId: this.$route.params.brandId,
                    categoryId: this.$route.params.categoryId
                },
                query: {
                    cmFactory: item.cm_factory,
                    cmFactoryId: item.cm_factory_id,
                    cmModel: item.cm_model,
                    cmModelId: item.cm_model_id,
                    cmBrandId: item.cm_brand_id,
                    cmBrand: item.cm_brand
                }
            });
        }
    }
};



