<template>
    <h1 class="text-center logo-box rel">
        <button type="button" class="btn btn-blue btn-sm logo-box-btn" @click="routeTo('query')" v-if="!toggleHide">
            <span class="iconfont icon-dangqianpinlei m-r-10 f24"></span>切换品类
        </button>
        <img class="logo" v-bind:src="DONE_CURRENT_TENANT.brand_logo">
    </h1>
</template>

<script>
import { mapGetters } from 'vuex';
import * as types from '@/vuex/modules/soupei-types.js';
import router from '@/router';
export default {
    name: 'logoBox',
    data() {
        return {
            toggleHide: false,
            localCategoryId: 'set|'
                             + (this.$route.query.tenantId || this.$route.params.tenantId) + '|'
                             + (this.$route.query.brandId || this.$route.params.brandId) + '|'
                             + (this.$route.query.categoryId || this.$route.params.categoryId)
        };
    },
    computed: {
        ...mapGetters([types.DONE_CURRENT_TENANT])
    },
    mounted: function () {
        this.$nextTick(() => {
            this.pageInit();
        });
    },
    methods: {
        pageInit() {
            // 设定toggleHide状态
            let localCategory = JSON.parse(localStorage.getItem(this.localCategoryId));
            this.toggleHide = localCategory.toggleHide;
        },
        // 跳转页面
        routeTo(url) {
            // 返回品类选择时移除本地存储
            localStorage.removeItem(this.localCategoryId);
            // 页面跳转
            router.push({
                name: url,
                query: {
                    tenantId: this.$route.query.tenantId || this.$route.params.tenantId,
                    brandId: this.$route.query.brandId || this.$route.params.brandId,
                    categoryId: this.$route.query.categoryId || this.$route.params.categoryId
                }
            });
        }

    }

};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";

.logo-box {
    background-color: $color-F;
    padding-top: $s10;
    padding-bottom: $s10;
    margin: 0;
}

.logo-box-btn {
    position: absolute;
    left: 0;
    top: $s25;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.logo {
    width: $s240;
}
</style>



