<template>
    <div class="tabbar b1tc clearfix">
        <li class="f30 cur" v-bind:class="{'active': $route.name == 'queryCategory' ,'col-xs-6': $route.query.categoryCode == '1_biaosuxiang', 'col-xs-4': $route.query.categoryCode != '1_biaosuxiang'}" @click="routeTo('queryCategory')">
            <div class="b1rc">按车型</div>
        </li>
        <li class="f30 cur" v-bind:class="{'active': $route.name == 'searchVin' ,'col-xs-6': $route.query.categoryCode == '1_biaosuxiang', 'col-xs-4': $route.query.categoryCode != '1_biaosuxiang'}" @click="routeTo('searchVin')">
            <div class="b1rc">按VIN</div>
        </li>
        <li class="f30 cur" v-if="$route.query.categoryCode != '1_biaosuxiang'" v-bind:class="{'active': $route.name == 'searchCode' ,'col-xs-6': $route.query.categoryCode == '1_biaosuxiang', 'col-xs-4': $route.query.categoryCode != '1_biaosuxiang'}" @click="routeTo('searchCode')">
            <div>按编码</div>
        </li>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import * as types from '@/vuex/modules/soupei-types.js';
import router from '@/router';
import Util from '@/assets/js/utility';
export default {
    name: 'tabbar',
    data() {
        return {

        };
    },
    computed: {
        ...mapGetters([types.DONE_CURRENT_TENANT])
    },
    mounted: function () {
        this.$nextTick(() => {
            // 安卓系统，搜索时隐藏底部tab
            this.bottomResizeFn();
        });
    },
    methods: {
        bottomResizeFn() {
            let tabbar = document.querySelector('.tabbar');
            window.onresize = function () {
                let windowHeight = window.innerHeight;//获取窗口的可见高度,不是整个文档的高度
                let screenHeight = window.screen.availHeight;//获取浏览器的屏幕的可用高度
                let u = navigator.userAgent;
                if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
                    if (windowHeight <= screenHeight / 1.2) {
                        Util.addClass(tabbar, 'hide');
                    } else {
                        Util.removeClass(tabbar, 'hide');
                    }
                }
            };
        },
        // 跳转页面
        routeTo(url) {
            let obj = {
                tenantId: this.DONE_CURRENT_TENANT.tenant_id,
                brandId: this.DONE_CURRENT_TENANT.brand_id,
                categoryId: this.$route.query.categoryId || this.$route.params.categoryId
            };
            if (url == 'queryCategory') {
                obj.categoryCode = this.$route.query.categoryCode;
                router.push({
                    name: url,
                    query: obj
                });
            } else {
                router.push({
                    name: url,
                    params: obj,
                    query: {
                        categoryCode: this.$route.query.categoryCode
                    }
                });
            }

        }
    }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.tabbar {
    right: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    z-index: 1;
    background-color: $color-F;
    text-align: center;
    margin: 0 auto;
    max-width: 7.2rem;
    li {
        padding: $s30 0;
    }

    .active {
        color: $color-blue;
    }

}
</style>



