<template>
    <div class="query-category" v-title="DONE_CURRENT_TENANT" :data-title="DONE_CURRENT_TENANT.brand_name">
        <div class="rel">
        <!-- logo -->
        <logo-box></logo-box>

        <!-- search -->
        <div class="clearfix search-box">
            <div class="col-xs-12 m-t-25">
                <div class="input-group input-group-lg search-input-group">
                    <input type="text" class="form-control input-text" maxlength="20" placeholder="输入车型查询配件，如CRV、杰德" v-model="modelSearch" v-on:input="vinInputFn()">
                    <span class="input-group-btn">
                        <span class="iconfont icon-dela" @click="clearInputFn" v-if="modelSearch"></span>
                        <button class="btn btn-blue input-text" type="button" v-on:click="searchFn()">查询</button>
                    </span>
                </div>
                <div class="rel">
                    <ul class="search-list" v-if="modelSearch && modelSearchList.length > 0">
                        <li class="list cur ell" v-for="item in modelSearchList" :key="item.cmBrand" @click="routeToChooseCondition(item)">
                            <span v-text="item.cm_brand + ' ' + item.cm_factory + ' ' + item.cm_model"></span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="box" v-if="carBrandList.length">

            <!-- 车型列表 -->
            <mt-navbar v-model="selected" class="box-title">
                <div class="col-xs-4" :class="{'blue': items.cm_brand_country_id == selected}" v-for="items in carBrandList" :key="items.cm_brand_country_id" @click="changeBtn(items)">
                    <span class="iconfont f24 m-r-5" :class="{'icon-down': items.cm_brand_country_id == selected, 'icon-up': items.cm_brand_country_id != selected}"></span> {{items.cm_brand_country}}
                </div>
            </mt-navbar>

            <!-- tab-container -->
            <mt-tab-container v-model="selected" :swipeable="true" class="box-content">
                <mt-tab-container-item :id="items.cm_brand_country_id" v-for="items in carBrandList" :key="items.cm_brand_country_id">
                    <ul class="row">
                        <li class="col-xs-4 f24 m-t-5 m-b-5" v-for="(item, index) in items.list" v-bind:key="index" v-if="index < items.len">
                            <div class="cur list-row" v-text="item.cm_brand" v-on:click="routeTo('chooseModel', item)"></div>
                        </li>
                        <li class="col-xs-4 f24 m-t-5 m-b-5 pull-right" v-if="items.list.length > 5">
                            <div class="cur list-row hella-blue" v-on:click="filterItems(items)" v-text="items.moreText"></div>
                        </li>
                    </ul>
                </mt-tab-container-item>
            </mt-tab-container>

        </div>

        <!-- 搜索输入有结果时禁用其他控件  -->
        <div class="mask" v-if="modelSearch && modelSearchList.length > 0"></div>

        <!-- 帐号提示  -->
        <accountTip v-on:message="setTipState" v-if="accountTipState"></accountTip>
    </div>

    <!-- 底部导航  -->
    <tabbar></tabbar>
    </div>
</template>

<script>
import Tabbar from '@/components/common/Tabbar';
import LogoBox from '@/components/common/LogoBox';
import AccountTip from '@/components/common/AccountTip';

import pageSet from '../js/queryCategory';

export default {
    name: 'queryCategory',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    watch: pageSet.watch,
    methods: pageSet.methods,
    components: { Tabbar , LogoBox, AccountTip}
};

</script>

<style lang="scss" scoped>
@import "../../../assets/css/config.scss";
.input-text.form-control,
.input-group-btn .input-text.btn {
    font-size: $s28;
}

.icon-dela{
    position: absolute;
    z-index: 10;
    left: -$s40;
    top: $s35;
    font-size: $s24;
    color: $color-C;
}
.search-input-group{
    position: relative;
    z-index: 11;
}
.search-list{
    border: solid 1px $color-C;
    width: 100%;
    max-height: $s525;
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
    position: absolute;
    top: 10px;
    left: 0;
    background-color: $color-F;
    z-index: 11;
}
.search-list .list {
    font-size: $s30;
    padding: $s15 $s15;
    border-bottom: solid 1px $color-C;
    color: $color-6;
}

.list-row {
    background-color: $color-F;
    border: solid 1px $color-C;
    border-radius: $s10;
    text-align: center;
    line-height: $s60;
}

.box-title {
    border-bottom: solid 1px $color-C;
    padding-left: $s20;
    padding-right: $s20;
    .col-xs-4 {
        padding: $s15 $s10;
    }
}

.box {
    margin-top: $s25;
    margin-left: $s30;
    margin-right: $s30;
    border: 1px solid $color-C;
}

.box-content {
    overflow-y: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    .mint-tab-container-wrap ul {
        padding: $s10 $s35;
    }
    .col-xs-4 {
        padding: 0 $s10;
    }
}

.mask{
    position: fixed;
    background: rgba(0, 0, 0, 0.01);
    width: 100%;
    height: 100%;
    z-index: 10;
    top: 0;
    left: 0;
}


</style>



