import { Toast } from 'mint-ui';
import { mapGetters } from 'vuex';
import * as types from '@/vuex/modules/soupei-types.js';
import router from '@/router';
import errCode from '@/assets/js/errCode';
import ENV from '@/assets/js/env';
import Util from '@/assets/js/utility';
let timer;

export default {
    name: 'queryCategory',
    data() {
        return {
            modelSearch: '',
            modelSearchList: [],
            delay: 0,
            selected: '',
            carBrandList: [],
            len: 8,
            accountTipState: ''
        };
    },
    mounted() {
        this.$nextTick(() => {
            this.getCarBrandFn();
            // 数据上报
            Util.onEvent('产品首页');
        });
    },
    computed: {
        ...mapGetters([types.DONE_CURRENT_TENANT])
    },
    watch: {
        modelSearch:function (val, oldVal) {
            this.delay = !oldVal ? 0 : 500;
        }
    },
    methods: {
        // 获取主机厂品牌列表
        getCarBrandFn() {
            this.$http.get(ENV.queryApi + 'carbrand/' + this.$route.query.tenantId + '/' + this.$route.query.brandId + '/' + this.$route.query.categoryId)
                .then((res) => {
                    if (res.data.code === 0) {
                        for (let i = 0; i < res.data.data.length; i++) {
                            let data = res.data.data[i];
                            data.len = this.len;
                            data.moreText = '点击更多';
                            this.carBrandList.push(data);
                        }
                        // 按车系排序
                        this.carBrandList.sort(Util.sortBySerial);
                    } else {
                        Toast({
                            message: errCode(res.data.code),
                            iconClass: 'iconfont icon-point f80'
                        });
                    }
                })
                .catch(() => {
                    Toast({
                        message: errCode(),
                        iconClass: 'iconfont icon-point f80'
                    });
                });
        },

        // 切换显示数据
        filterItems(items) {
            if (items.len > this.len) {
                items.len = this.len;
                items.moreText = '点击更多';
            } else {
                items.len = items.list.length;
                items.moreText = '收起目录';
            }
        },

        // 切换品牌tab按钮状态
        changeBtn(items) {
            if(this.selected == items.cm_brand_country_id){
                this.selected = '';
                this.setBoxHeight(0);
            }else{
                this.selected = items.cm_brand_country_id;
                this.setBoxHeight();
            }
        },

        // 设定品牌选择框高度
        setBoxHeight(hei) {
            let boxContent = document.querySelector('.box-content');
            if(hei == undefined){
                let logoBox = document.querySelector('.logo-box').offsetHeight;
                let searchBox = document.querySelector('.search-box').offsetHeight;
                let boxTitle = document.querySelector('.box-title').offsetHeight;
                let tabBar = document.querySelector('.tabbar').offsetHeight;
                let docHei = document.documentElement.offsetHeight;
                boxContent.style.height = (docHei - logoBox - searchBox - boxTitle - tabBar - 50) + 'px';
            }else{
                boxContent.style.height = hei;
            }
        },

        // 确定搜索
        searchFn() {
            if (!this.modelSearch) return;
            switch (this.modelSearchList.length) {
            case 0:
                this.routeTo('searchModel');
                // 数据上报
                Util.onEvent('搜索车型 | 无结果反馈');
                break;
            case 1:
                this.routeToChooseCondition(this.modelSearchList[0]);
                break;
            }
        },

        // 搜索输入匹配
        vinInputFn() {
            this.handleSearch();
        },

        // 处理延时请求搜索接口
        handleSearch() {
            clearTimeout(timer);
            timer = setTimeout(() => {
                this.getSearchFn();
            }, this.delay);
        },

        // 获取搜索结果/query/model/search?keywords
        getSearchFn() {
            let modelSearch = Util.trim(this.modelSearch);
            if(!modelSearch) return;

            this.$http.get(ENV.queryApi + 'query/model/search/' + this.$route.query.tenantId + '/' + this.$route.query.brandId + '?keywords=' + modelSearch)
                .then((res) => {
                    if (res.data.code === 0) {
                        this.modelSearchList = res.data.data;
                    } else {
                        Toast({
                            message: errCode(res.data.code),
                            iconClass: 'iconfont icon-point f80'
                        });
                    }
                })
                .catch(() => {
                    Toast({
                        message: errCode(),
                        iconClass: 'iconfont icon-point f80'
                    });
                });
        },

        // 清除输入框
        clearInputFn(){
            this.modelSearch = '';
        },

        // 搜索结果跳转
        routeToChooseCondition(item){
            router.push({
                name: 'chooseCondition',
                params: {
                    tenantId: this.$route.query.tenantId,
                    brandId: this.$route.query.brandId,
                    categoryId: this.$route.query.categoryId
                },
                query: {
                    cmFactory: item.cm_factory,
                    cmFactoryId: item.cm_factory_id,
                    cmModel: item.cm_model,
                    cmModelId: item.cm_model_id,
                    cmBrandId: item.cm_brand_id,
                    cmBrand: item.cm_brand,
                    keyword: item.cm_factory + item.cm_model
                }
            });
        },

        // 点击事件
        handleClick(state) {
            switch (state) {
            case 'modelSearch':
                this.routeTo('chooseFactory');
                break;
            case 'vinSearch':
                this.routeTo('searchVin');
                break;
            case 'codeSearch':
                this.routeTo('searchCode');
                break;
            }
        },

        // 更改帐号提示状态值
        setTipState() {
            this.accountTipState = !this.accountTipState;
        },

        // 跳转页面
        routeTo(url, brand) {
            if (brand) {
                router.push({
                    name: url,
                    params: {
                        tenantId: this.$route.query.tenantId,
                        brandId: this.$route.query.brandId,
                        categoryId: this.$route.query.categoryId
                    },
                    query: {
                        cmBrandId: brand.cm_brand_id,
                        cmBrand: brand.cm_brand,
                        cmModelId: brand.cm_model_id
                    }
                });
            } else {
                router.push({
                    name: url,
                    params: {
                        tenantId: this.$route.query.tenantId,
                        brandId: this.$route.query.brandId,
                        categoryId: this.$route.query.categoryId
                    }
                });
            }
        }



    }
};



