import { Toast } from 'mint-ui';
import router from '@/router';
import { mapGetters } from 'vuex';
import * as types from '@/vuex/modules/soupei-types.js';
import Util from '@/assets/js/utility';

export default {
    name: 'search',
    data() {
        return {
            vinCode: '',
            partskuCode: ''
        };
    },
    watch: {
        '$route.name': function (val) {
            // 解决同组件路由切换title不更新
            document.title = val == 'searchVin' ? '按VIN查询' : '按编码查询';
            // 数据上报
            this.onEventFn(this.$route.name);
        }
    },
    mounted() {
        this.$nextTick(() => {
            // 数据上报(这里只会执行一次，后续调查问题，暂时watch解决)
            this.onEventFn(this.$route.name);
        });
    },
    computed: {
        ...mapGetters([types.DONE_CURRENT_TENANT])
    },
    methods: {
        // 处理VIN字母显示形式
        vinInputFn() {
            let text = this.filterVin(this.vinCode);
            if (text) {
                var list = [],
                    i = 0,
                    j = 4;
                while (i < text.length) {
                    if (i < j * 3) {
                        list.push(text.substr(i, j));
                        i += j;
                    } else {
                        list.push(text.substr(i));
                        break;
                    }
                }
                this.vinCode = list.join('-').toUpperCase();
            }
        },

        // vin码去除-符、空格和中文字符
        filterVin(vin) {
            return vin.replace(/-|(^\s+)|(\s+$)|[^\x00-\xff]/g, '');
        },

        // 处理编码查询字母大写
        partskuCodeInputFn() {
            this.partskuCode = this.partskuCode.toUpperCase();
        },

        // 搜索
        searchFn(str) {
            if (str == 'searchVin') {
                // 按VIN查询
                let code = this.filterVin(this.vinCode);
                if (code.length == 17) {
                    this.routeTo('searchVinList', code);
                } else {
                    Toast({
                        message: '请正确输入字符',
                        iconClass: 'iconfont icon-point f80'
                    });
                    this.vinCode = '';
                }

            } else {
                // 按编码查询
                this.routeTo('queryList', this.partskuCode);
            }
        },

        // 数据上报
        onEventFn(routeName) {
            switch (routeName) {
            case 'searchVin':
                Util.onEvent('按VIN查询');
                break;
            case 'searchCode':
                Util.onEvent('按编码查询');
                break;
            }
        },

        // 跳转页面
        routeTo(url, code) {
            let obj = url == 'searchVinList' ?
                { searchCode: code } :
                { part_sku_code: code };

            router.push({
                name: url,
                params: {
                    tenantId: this.$route.params.tenantId,
                    brandId: this.$route.params.brandId,
                    categoryId: this.$route.params.categoryId
                },
                query: obj
            });
        }
    }
};



